.WIP {
  min-height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #53b3cb;
}

.container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  margin: auto;
  max-width: 800px;
}

.activity-suggestion {
  color: #53b3cb;
  display: flex;
  flex-direction: column;
  margin: 80px 24px 0px;
  text-align: center;
}

.avatar-container {
  align-self: center;
  justify-self: center;
}

.avatar-img {
  border-radius: 100%;
  width: 150px;
  border: 2px solid #53b3cb;
}

body {
  background-color: #0c090d;
}
